body {
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

main {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}

.black-text a {
  color: black;
}

.black-text a:hover {
  color: teal;
}

a circle-loader {
  position: absolute;
  top: 0;
  left: 40%;
}

.loading {
  opacity: 0.7;
  cursor: default;
}

main-header .progress {
  margin: 0 !important;
}

#insta-logo {
  background: url(assets/img/insta-sprites.png) -177px -264px;
  height: 35px;
  width: 176px;
}

.error-message {
  color: red;
}

.profile-item-text.clickable {
  cursor: pointer;
}

.profile-item-text.clickable:hover {
  color: teal;
}

.modal-followers .modal-title {
  position: absolute;
  top: 0;
  font-weight: bolder;
  width: 100%;
  padding-left: 5vw;
  height: 7vmax;
  min-height: 7vmax;
  max-height: 7vmax;
}

.modal-followers .modal-content {
  padding: 0;
  top: 7vmax;
  border-top: solid 2px #dbdbdb;
  max-height: calc(100% - 56px - 7vmax) !important;
}

.modal-followers .collection {
  border: 0;
}

